import { useEffect,useState } from "react"



const UseEffectApi = () => {
const[users,setUsers]=useState([]);

const getusers= async ()=>{
const response=await fetch('https://restcountries.com/v2/continent/asia');
setUsers(await response.json());
}




useEffect(() => {
    getusers();
    }
, [])


    return (
        <>

        <h1 className="display-2 head">Countries in Asia</h1>
        <div className="row text-center">

        
       {
users.map((curEle)=>{

  return(
<div className="col-10 col-md-3 mt-5">

<div className="card" >
  <img src={curEle.flags[0]} className="card-img-top img-fluid img-thumbnail" alt="..."/>
  <div className="card-body">
    <p className="card-text"> Name : {curEle.name}</p>
    <p className="card-text">Region : {curEle.region}</p>
    <p className="card-text"> Capital : {curEle.capital}</p>
    <p className="card-text"> Population : {curEle.population}</p>
  </div>
</div>
</div>

  )

}

)


       }
       </div>
        </>
    )
}

export default UseEffectApi
